import React from 'react';
import PropTypes from 'prop-types';
import { CellTreeView as RealComponent } from '../LazyLoader';

/**
 * CellTreeView is a custom TreeView component for hierarchically grouping cell types.
 * It displays cell types in a hierarchical structure (Major → Minor → Sub) and allows
 * users to create groups, rename them, and reorganize the hierarchy via drag & drop.
 * Data is provided as CSV-formatted strings with columns: celltype_major, celltype_minor, celltype_sub.
 */
const CellTreeView = (props) => {
    return (
        <React.Suspense fallback={null}>
            <RealComponent {...props}/>
        </React.Suspense>
    );
};

CellTreeView.defaultProps = {
    selectedItems: [],
    searchTerm: '',
    selectionMode: 'multi'
};

CellTreeView.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * Initial cell hierarchy data as CSV string with columns: celltype_major, celltype_minor, celltype_sub
     */
    initialData: PropTypes.string,

    /**
     * Grouped cell hierarchy data as CSV string with columns: celltype_major, celltype_minor, celltype_sub
     */
    groupedData: PropTypes.string,

    /**
     * Array of selected item IDs
     */
    selectedItems: PropTypes.arrayOf(PropTypes.string),

    /**
     * Search term to filter the tree
     */
    searchTerm: PropTypes.string,

    /**
     * Selection mode: 'multi' (default), 'single', or 'none'
     */
    selectionMode: PropTypes.oneOf(['multi', 'single', 'none']),

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};

export default CellTreeView;

export const defaultProps = CellTreeView.defaultProps;
export const propTypes = CellTreeView.propTypes;
